import { reduxForm, formValueSelector } from 'redux-form'
import { connect } from 'react-redux'
import { withRouter } from 'react-router-dom'
import InitResetPasswordForm from 'Shared/forms/ResetPassword/InitResetPasswordForm'
import validate from 'Shared/validators/login'
import AuthService from 'Shared/services/AuthService'

const authService = new AuthService()

export const formName = 'CandidateResetPassword'

let Form = reduxForm({
  form: formName,
  validate
})(InitResetPasswordForm)

const mapStateToProps = state => ({
  email: formValueSelector(formName)(state, 'email')
})

const mapDispatchToProps = dispatch => ({
  sendResetPasswordRequest: values => {
    try {
      authService.initResetPasswordCandidate(values.email)
    } catch (err) {
      return console.error(err)
    }
  }
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



